.Divider{width:100%;display:flex;align-items:center;text-align:center;margin:16px 0}.Divider::before{background:#ff6900;content:"";flex:1 1;height:0.1rem;margin:0 2rem;width:100%}.Divider::after{background:#ff6900;content:"";flex:1 1;height:0.1rem;margin:0 2rem;width:100%}

.SizePicker{width:100%}.SizePicker__dishes{width:100%;display:flex;justify-content:space-evenly;align-items:center;max-width:40rem;overflow:hidden;padding:0 20px;margin:0 auto}.SizePicker__size-dish{align-items:center;border-radius:50%;cursor:pointer;display:flex;justify-content:center;transition:all 0.2s}.SizePicker__size-dish--small{height:60px;width:60px;box-shadow:inset 0 0 0 7.5px #FFB10B}.SizePicker__size-dish--medium{height:90px;width:90px;box-shadow:inset 0 0 0 10px #FFB10B}.SizePicker__size-dish--large{height:120px;width:120px;box-shadow:inset 0 0 0 12.5px #FFB10B}.SizePicker__size-dish.active{background:linear-gradient(45deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(-45deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(90deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(180deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),#FFD72C;border-radius:100%}

.PizzaToggle{display:inline-block;float:left;background:#fff;width:250px;height:100px;box-shadow:0 5px 25px -15px #676550;position:relative;margin:50px 20px 0px;border-radius:100px;opacity:0;animation:fadein 0.5s ease-in 1 forwards}.PizzaToggle__title{position:absolute;color:#e1dea9;color:white;font-family:"Nunito";letter-spacing:3px;width:100%;text-align:center;top:-30px;font-size:24px}.PizzaToggle__topping{transform:scale(0) rotate(0deg);transition:0.15s ease-in-out}.PizzaToggle__topping:nth-of-type(1){left:18px;top:14px;margin-top:10.5px;margin-left:5px}.PizzaToggle__topping:nth-of-type(2){left:36px;top:28px;margin-top:10.5px;margin-left:5px}.PizzaToggle__topping:nth-of-type(3){left:54px;top:42px;margin-top:10.5px;margin-left:5px}.PizzaToggle__topping:nth-of-type(4){left:72px;top:56px;margin-top:10.5px;margin-left:5px}.PizzaToggle__topping:nth-of-type(5){left:90px;top:70px;margin-top:10.5px;margin-left:5px}.PizzaToggle__topping:nth-of-type(4){left:55px;top:28px;margin-top:0;margin-left:0}.PizzaToggle__topping:nth-of-type(5){left:25px;top:58px;margin-top:0;margin-left:0}.PizzaToggle:nth-of-type(1){animation-delay:.1s}.PizzaToggle:nth-of-type(2){animation-delay:.2s}.PizzaToggle:nth-of-type(3){animation-delay:.3s}.PizzaToggle:nth-of-type(4){animation-delay:.4s}.PizzaToggle:nth-of-type(5){animation-delay:.5s}.PizzaToggle:nth-of-type(6){animation-delay:.6s}.PizzaToggle_toppingShape_round .PizzaToggle__topping{position:absolute;width:10px;height:10px;border-radius:100%;background:#DC453F}.PizzaToggle_toppingShape_round.PizzaToggle_toppingName_extraCheese .PizzaToggle__topping{background:yellow}.PizzaToggle_toppingShape_round.PizzaToggle_toppingName_blackOlives .PizzaToggle__topping{width:6px;height:6px;background:black}.PizzaToggle_toppingShape_round.PizzaToggle_toppingName_blackOlives .PizzaToggle__topping:before{content:'';position:absolute;width:5px;height:5px;top:-5px;left:8px;background:black;border-radius:100%}.PizzaToggle_toppingShape_round.PizzaToggle_toppingName_blackOlives .PizzaToggle__topping:after{content:'';position:absolute;width:4px;height:4px;top:9px;left:12px;background:black;border-radius:100%}.PizzaToggle_toppingShape_round.PizzaToggle_toppingName_spinach .PizzaToggle__topping{background:green}.PizzaToggle_toppingShape_ring .PizzaToggle__topping{position:absolute;width:10px;height:10px;border-radius:100%;background:white}.PizzaToggle_toppingShape_ring .PizzaToggle__topping:after{content:'';position:absolute;width:6px;height:6px;top:2px;left:2px;background:#FFD72C;border-radius:100%}.PizzaToggle_toppingShape_oval .PizzaToggle__topping{position:absolute;width:10px;height:5px;background:#b0926d;border-radius:5px 5px 0 0}.PizzaToggle_toppingShape_oval .PizzaToggle__topping:before{content:'';position:absolute;width:50%;height:75%;border-radius:15px 15px 0 0;background:#b0926d;bottom:0;left:-2.5px}.PizzaToggle_toppingShape_square .PizzaToggle__topping{position:absolute;width:5px;height:5px;background:#ffffa2}.PizzaToggle_toppingShape_square .PizzaToggle__topping:before{content:'';position:absolute;width:50%;height:75%;border-radius:0;background:#ffff3c;bottom:0;left:-2.5px}.PizzaToggle_toppingShape_square.PizzaToggle_toppingName_bacon .PizzaToggle__topping{background:red}.PizzaToggle_toppingShape_square.PizzaToggle_toppingName_bacon .PizzaToggle__topping:before{background:white}.PizzaToggle_toppingShape_square.PizzaToggle_toppingName_extraCheese .PizzaToggle__topping{background:yellow}.PizzaToggle_toppingShape_square.PizzaToggle_toppingName_extraCheese .PizzaToggle__topping:before{background:yellow}.PizzaToggle_toppingShape_gull .PizzaToggle__topping{position:absolute;width:10px;height:10px;background:#FFD72C;border-radius:100%;box-shadow:inset 3px 0 0 #4cad7b}.PizzaToggle_toppingShape_gull .PizzaToggle__topping:before{content:'';position:absolute;width:7px;height:7px;top:-5px;background:#FFD72C;border-radius:100%;box-shadow:inset 3px 0 0 #4cad7b}.PizzaToggle_toppingShape_gull.PizzaToggle_toppingName_mushrooms .PizzaToggle__topping{box-shadow:inset 3px 0 0 white}.PizzaToggle_toppingShape_gull.PizzaToggle_toppingName_mushrooms .PizzaToggle__topping:before{box-shadow:inset 3px 0 0 white}.PizzaToggle_pizzaShape_big input:checked ~ .PizzaToggle__pizza[class]{animation:pizzaflip2 0.75s linear 1 forwards}@keyframes pizzaflip2{0%{transform:translateX(0) scale(1)}25%{transform:translateX(75px) translateY(-15px) scale(1.5)}75%{transform:translateX(100px) translateY(-15px) scale(1.5)}100%{transform:translateX(150px) scale(1.35) rotateX(-20deg)}}.PizzaToggle_pizzaShape_thick input:checked ~ .PizzaToggle__pizza .PizzaToggle__inner[class]{animation:spinwobble2 0.9s linear 1 forwards}@keyframes spinwobble2{0%{transform:rotateX(0deg) rotate(0deg)}40%{transform:rotateX(-30deg) rotate(180deg)}60%{transform:rotateX(-30deg) rotate(360deg)}80%{transform:rotateX(30deg) rotate(360deg);box-shadow:inset 0 0 0 10px #FFB10B,inset 0 0 0px 0px #FFD72C,0 0px 0px #d79200,0 0px #ca8a00;background:linear-gradient(45deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(-45deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(90deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(180deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),#FFD72C}100%{transform:rotateX(0deg) rotate(360deg) translateY(-20px);box-shadow:inset 0 0 0 10px #FFB10B,inset 0 0 5px 13px #FFD72C,0 10px 5px #d79200,0 15px #ca8a00}}@keyframes fadein{0%{opacity:0}75%{opacity:0;transform:scale(0)}100%{opacity:1;transform:scale(1)}}.PizzaToggle input{opacity:0;display:block;width:100%;height:100%;position:absolute;left:0;top:0;z-index:999}.PizzaToggle__shadow{position:absolute;z-index:0;width:10px;height:10px;border-radius:100%;background:#676550;top:calc(50% - 5px);left:45px;box-shadow:0 10px 30px 30px #676550;transition:0.75s linear;opacity:0.25;animation:flipback 0.25s linear 1 forwards}.PizzaToggle__pizza{position:absolute;left:5px;top:5px;width:90px;height:90px;border-radius:100%;transition:0.2s ease-in-out;transform:translateX(0);animation:flipback 0.25s linear 1 forwards;transform-style:preserve-3d;perspective:10000px}@keyframes flipback{0%{transform:translateX(150px) scale(1)}100%{transform:translateX(0px) scale(1)}}.PizzaToggle__inner{position:absolute;width:100%;height:100%;background:linear-gradient(45deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(-45deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(90deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),linear-gradient(180deg, rgba(0,0,0,0), rgba(0,0,0,0) 49%, #f8c900 49%, #f8c900 51%, rgba(0,0,0,0) 51%),#FFD72C;box-shadow:inset 0 0 0 10px #FFB10B;border-radius:100%}.PizzaToggle input:checked ~ .PizzaToggle__shadow{animation:shadowslide 0.75s linear 1 forwards}@keyframes shadowslide{0%{transform:translateX(0) scale(1)}25%{transform:translateX(75px) translateY(20px) scale(0.75)}75%{transform:translateX(100px) translateY(20px) scale(0.75)}100%{transform:translateX(150px) scale(1)}}.PizzaToggle input:checked ~ .PizzaToggle__pizza{animation:pizzaflip 0.75s linear 1 forwards}.PizzaToggle input:checked ~ .PizzaToggle__pizza .PizzaToggle__topping:nth-of-type(1){transition-delay:.775s;transform:scale(1) rotate(40deg)}.PizzaToggle input:checked ~ .PizzaToggle__pizza .PizzaToggle__topping:nth-of-type(2){transition-delay:.8s;transform:scale(1) rotate(80deg)}.PizzaToggle input:checked ~ .PizzaToggle__pizza .PizzaToggle__topping:nth-of-type(3){transition-delay:.825s;transform:scale(1) rotate(120deg)}.PizzaToggle input:checked ~ .PizzaToggle__pizza .PizzaToggle__topping:nth-of-type(4){transition-delay:.85s;transform:scale(1) rotate(160deg)}.PizzaToggle input:checked ~ .PizzaToggle__pizza .PizzaToggle__topping:nth-of-type(5){transition-delay:.875s;transform:scale(1) rotate(200deg)}@keyframes pizzaflip{0%{transform:translateX(0) scale(1)}25%{transform:translateX(75px) translateY(-15px) scale(1.5)}75%{transform:translateX(100px) translateY(-15px) scale(1.5)}100%{transform:translateX(150px) scale(1)}}.PizzaToggle input:checked ~ .PizzaToggle__pizza .PizzaToggle__inner{animation:spinwobble 0.75s linear 1 forwards}@keyframes spinwobble{0%{transform:rotateX(0deg) rotate(0deg)}30%{transform:rotateX(-30deg) rotate(120deg)}70%{transform:rotateX(30deg) rotate(240deg)}100%{transform:rotateX(0deg) rotate(360deg)}}

.CardToggle{display:flex;flex-direction:column;align-items:center;width:300px;height:300px}.CardToggle__img{min-height:300px;width:100%}.CardToggle__PizzaToggle[class]{margin-top:-120px}

.ToppingsPicker{width:100%}.ToppingsPicker__list{width:100%;display:flex;flex-wrap:wrap;justify-content:space-around;padding:0 20px 20px}

.PizzaForm{flex-grow:1;background:#fcfcfc;overflow-y:scroll}@media (max-width: 850px){.PizzaForm{overflow:unset}}.PizzaForm__inner{display:flex;flex-direction:column;align-items:center}

.PizzaPreview{position:relative;min-width:500px;flex-basis:500px;flex-shrink:0;display:flex;justify-content:center;align-items:center}@media (max-width: 500px){.PizzaPreview{min-width:100vw;width:100vw}}.PizzaPreview__part{position:absolute}@media (max-width: 500px){.PizzaPreview__part{min-width:100vw;width:100vw}}

.OrderInfo{height:100%}.OrderInfo__item{display:flex;justify-content:space-between}.OrderInfo__total{display:flex;justify-content:space-between;font-weight:bold;margin-top:1.5rem}

.OrderButton{height:100px;padding:12px 24px;font-size:24px;line-height:24px;background-color:#ff6900;color:#fff;outline:none;border:none;border-radius:9999px;text-align:center;font-weight:500;position:relative;overflow:hidden;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;transition-property:background-color, color;transition-duration:200ms;transition-timing-function:ease-out;box-shadow:0 5px 25px -15px #676550}.OrderButton:hover{background-color:#e86100;color:#fff}

.OrderSection{width:100%;flex:1 1;padding:1.25rem 2.5rem;display:flex;flex-direction:column}

.PizzaDisplay{display:flex;flex-direction:column}

*,*::before,*::after{box-sizing:border-box;margin:0;padding:0;text-overflow:ellipsis}html,body{box-sizing:border-box;font-family:"Montserrat", Arial, Helvetica, sans-serif;margin:0}body{color:#4a5667}::-webkit-scrollbar{width:0.3rem}::-webkit-scrollbar-track{background:#f7fafc}::-webkit-scrollbar-thumb{background:#ff6900}::-webkit-scrollbar-thumb:hover{background:white}.app{height:100vh;width:100vw;display:flex}@media (max-width: 850px){.app{flex-direction:column-reverse;overflow:auto}}

